@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;700);
.ManualInput .inputs{display:grid;grid-template-columns:1fr 1fr;margin:18px 0;grid-column-gap:18px;-webkit-column-gap:18px;column-gap:18px}.ManualInput .action{text-align:center}.ManualInput .action .compare{display:inline-block;margin:auto;padding:6px 12px;border:1px solid #fff;background-color:#282c34;color:#fff;cursor:pointer}.SingleInput textarea{width:100%;height:400px;font-family:Inconsolata,monospace}

.DisplayError{margin:18px 0}.DisplayError pre{color:#ef476f}.DisplayValue.left{background-color:#bf113a}.DisplayValue.right{background-color:#037356}.DisplayResult pre{margin:0;padding:3px}.DisplayResult.collapsed pre{cursor:pointer}.DisplayResult.collapsed pre:hover{background-color:#3e4451}.DisplayResult.collapsed.differ>pre{background-color:#f4a261}.DisplayResult.collapsed.differ>pre:hover{background-color:#f7be91}

.App{margin:18px}.App h1{margin:18px;text-align:center}

*,::before,::after{box-sizing:border-box}body{margin:0;background-color:#282c34;color:#fff;font-family:Roboto,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

